import { LogLevels } from '@transai/logger';
import { NodeWrapperInterface } from './node-wrapper';

export const getLogLevel = (node: NodeWrapperInterface) => {
  let logLevel = (node.process.env.LOG_LEVEL as LogLevels) || LogLevels.info;
  const validLogLevels = Object.values(LogLevels);
  const faultyLogLevel = !validLogLevels.includes(logLevel);

  if (faultyLogLevel) {
    logLevel = LogLevels.info;
  }

  if (faultyLogLevel) {
    console.error(
      `Invalid log level: ${logLevel}; only allowed values are 'error', 'warn', 'info', 'debug', 'trace'. Using 'info' as the default.`,
    );
  }

  return logLevel;
};
